import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Key } from 'shared-code-lib';
import { FolderType, IFolder } from './../../../../blocks/services/models';

@Component({
  selector: '[appBoxHead]',
  templateUrl: './box-head.component.html',
  styleUrls: ['./box-head.component.scss'],
})
export class BoxHeadComponent implements OnInit {
  @Input() folder: IFolder;

  folders: Array<IFolder>;

  dropdownSelectedItemIndex = -1;

  constructor(private router: Router) {}

  ngOnInit() {}

  getFolders = (): Array<IFolder> => {
    return this.folder.product.folders
      .filter((f) => f.id != this.folder.id && f.folderType == this.folder.folderType && f.showViewDropList)
      .sort((x, y) => x.name.localeCompare(y.name));
  };

  getFolderRouting = (folder: IFolder): string => {
    if (folder.folderType == FolderType.Product) {
      return `/${this.folder.product.id}/${folder.id}`;
    } else if (folder.folderType == FolderType.Settings) {
      return `/settings/${this.folder.product.id}/${folder.id}`;
    }

    return '';
  };

  onKeyDown = (e: KeyboardEvent) => {
    const folders = this.getFolders();

    if (e.key == Key.ArrowUp) {
      this.dropdownSelectedItemIndex--;
      if (this.dropdownSelectedItemIndex < 0) this.dropdownSelectedItemIndex = folders.length - 1;
    } else if (e.key == Key.ArrowDown) {
      this.dropdownSelectedItemIndex++;
      if (this.dropdownSelectedItemIndex == folders.length) this.dropdownSelectedItemIndex = 0;
    } else if (e.key == Key.Enter) {
      const currentFolder = folders[this.dropdownSelectedItemIndex];
      const route = [this.getFolderRouting(currentFolder)];
      this.router.navigate(route);
    }
  };

  onMouseOver = (itemIndex: number) => {
    this.dropdownSelectedItemIndex = itemIndex;
  };

  resetDropdownSelectedItem() {
    this.dropdownSelectedItemIndex = -1;
  }
}
