import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { ApiService } from '../../services/api.service';
import { FolderDetails } from '../../services/models';
import { FolderDetailsService } from '../../services/folder-details.service';

@Component({
  selector: 'app-folder-details',
  templateUrl: './folder-details.component.html',
  styleUrls: ['./folder-details.component.scss'],
})
export class FolderDetailsComponent implements OnInit, OnDestroy {
  @Input() details: FolderDetails;
  data: any;
  subscriptions = new Subscription();

  constructor(private apiService: ApiService, private folderDetails: FolderDetailsService) {}

  ngOnInit(): void {
    this.subscriptions.add(
      this.folderDetails.folderDetailsChanged.subscribe((folderData) => {
        if (folderData.folderDetailsId == this.details.id) {
          this.data = folderData;
        }
      })
    );
  }

  ngOnDestroy() {
    this.subscriptions.unsubscribe();
  }

  loaded = (d: any): void => {
    this.data = d;
  };

  failed = (e: any): void => {
    console.log('FolderDetailsComponent', e);
  };
}
