import { Component, Input, OnInit } from '@angular/core';
import { getLocalizedCurrencyName } from '../../../../../../../Bamba.Shared/src/utils/currency';
import { UserStateService } from '../../services/user-state.service';

@Component({
  selector: 'app-interfax-currency',
  templateUrl: './interfax-currency.component.html',
  styleUrls: ['./interfax-currency.component.scss'],
})
export class InterfaxCurrencyComponent implements OnInit {
  @Input() data: any;

  currency: string;
  constructor(private userState: UserStateService) {}

  ngOnInit(): void {
    this.currency = getLocalizedCurrencyName(this.data.currency, this.userState.getLocale());
  }
}
