import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdminMetaDataComponent } from './admin-metadata.component';

const routes: Routes = [
  {
    path: 'duplicate/:metadataid',
    component: AdminMetaDataComponent,
    data: { type: 'duplicate' },
    pathMatch: 'full',
  },
  {
    path: 'create',
    component: AdminMetaDataComponent,
    data: { type: 'create' },
    pathMatch: 'full',
  },
  {
    path: ':metadataid',
    component: AdminMetaDataComponent,
    data: { type: 'properties' },
    pathMatch: 'full',
  },
  {
    path: 'create/:metadatatemplate',
    component: AdminMetaDataComponent,
    data: { type: 'create' },
    pathMatch: 'full',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AdminMetaDataRoutingModule {}
