import { Component, Input, OnInit } from '@angular/core';
import { getLocalizedCurrencyName } from '../../../../../../../Bamba.Shared/src/utils/currency';
import { UserStateService } from '../../services/user-state.service';
import { IMoneyAmount } from 'shared-code-lib';

@Component({
  selector: 'app-interfax-prepaid-current-balance',
  templateUrl: './interfax-prepaid-current-balance.component.html',
  styleUrls: ['./interfax-prepaid-current-balance.component.scss'],
})
export class InterfaxPrepaidCurrentBalanceComponent implements OnInit {
  @Input() data: { balance: IMoneyAmount };

  currency: string;
  constructor(private userState: UserStateService) {}

  ngOnInit(): void {
    this.currency = getLocalizedCurrencyName(this.data.balance.currencyCode, this.userState.getLocale());
  }
}
