import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { NgxSmartModalComponent, NgxSmartModalService } from 'ngx-smart-modal';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import {
  DelayService,
  IColumn,
  IDefaultColumn,
  MetaDataDefinition,
  TableItem,
  UserPreferencesService,
} from 'shared-code-lib';
import { UserStateService } from '../../../../blocks/services/user-state.service';
import { Filter } from './../../../../blocks/models/filter';
import { Folder, IAction } from './../../../../blocks/services/models';

@Component({
  selector: '[appActionPanel]',
  templateUrl: './action-panel.component.html',
  styleUrls: ['./action-panel.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class ActionPanelComponent implements OnInit, AfterViewInit, OnDestroy {
  @Input() folder: Folder;
  @Input() selectedItems: Array<TableItem>;
  @Input() areFiltersPinned: boolean = null;

  @Input() hideFilters: boolean = false;
  @Input() hideColumns: boolean = false;

  @Output() onFiltersUpdated = new EventEmitter<void>();
  @Output() onActionInvoked = new EventEmitter<IAction>();
  @Output() onSaveColumns = new EventEmitter<Array<IDefaultColumn>>();
  @Output() onPinClicked = new EventEmitter<void>();

  @ViewChild('dualPickRef') dualPickRef: ElementRef;
  @ViewChild('saveModalButton', { static: true }) saveModalButton: ElementRef;

  modalColumnPropertiesInstance: NgxSmartModalComponent;
  modalColumnPropertiesVisible: boolean;

  availableColumns: Array<IColumn> = [];
  selectedColumns: Array<IColumn> = [];

  subscriptions = new Subscription();

  constructor(
    private userState: UserStateService,
    private translate: TranslateService,
    public ngxSmartModalService: NgxSmartModalService,
    private toastr: ToastrService,
    private delay: DelayService,
    private userPreferencesService: UserPreferencesService
  ) {}

  ngOnInit() {
    this.prepareColumnDialogData();
  }

  ngAfterViewInit(): void {
    this.modalColumnPropertiesInstance = this.ngxSmartModalService.getModal('modalColumnProperties');

    this.subscriptions.add(
      this.modalColumnPropertiesInstance.onOpen.subscribe((modal: NgxSmartModalComponent) => {
        this.modalColumnPropertiesVisible = true;
        this.prepareColumnDialogData();

        this.delay.execute(() => {
          this.saveModalButton.nativeElement.focus();
        }, 0);
      })
    );

    this.subscriptions.add(
      this.modalColumnPropertiesInstance.onClose.subscribe((modal: NgxSmartModalComponent) => {
        this.modalColumnPropertiesVisible = false;
      })
    );
  }

  displayColumnProperties = () => {
    this.modalColumnPropertiesInstance.open();
  };

  onToolbarAction = (action: IAction) => {
    this.onActionInvoked.emit(action);
  };

  savePreference = () => {
    if (this.selectedColumns.length === 0) {
      this.toastr.warning(this.translate.instant('Action-panel.NeedsOneColumnWarning'));
      return;
    }

    this.closeColumnPropertiesInstance();

    const preference: IDefaultColumn[] = [];

    this.selectedColumns.forEach((element) => {
      const elementDefaultColumn = this.folder.defaultColumns.find((dc) => dc.id == element.id) || {
        id: element.id,
        width: 200,
        stretch: true,
      };

      preference.push(elementDefaultColumn);
    });

    this.onSaveColumns.emit(preference);
  };

  closeColumnPropertiesInstance = () => {
    this.modalColumnPropertiesInstance.close();
  };

  resetColumns = () => {
    if (!this.folder) return;

    this.prepareColumnDialogData(true);
  };

  prepareColumnDialogData = (reset = false) => {
    if (!this.folder) return;

    let preferred: Array<IDefaultColumn> = this.userPreferencesService.getPreference(this.getColumnKey());

    if (!preferred || reset) preferred = this.folder.defaultColumns;

    let folderColumns: IColumn[] = [];
    if (this.folder && this.folder.columns) {
      this.folder.columns.forEach((c) => folderColumns.push(c));
    }

    if (this.folder.includeMetaDataAsColumns) {
      this.userState.user.metaData.forEach((md) => {
        folderColumns.push(MetaDataDefinition.asIColumn(md));
      });
    }

    const preferencesMapped = preferred
      .filter((pc) => pc != null)
      .map((pc: IDefaultColumn) => {
        for (let fc of folderColumns) {
          if (pc.id == fc.id || pc.id == fc.headerText.replace(/ /g, '_').toUpperCase()) return fc;
        }

        return null;
      })
      .filter((pc) => pc != null);

    this.availableColumns = folderColumns;
    this.selectedColumns = preferencesMapped;
  };

  getColumnKey = (): string => {
    return (
      'columns-' +
      this.folder.product.id +
      '-' +
      this.folder.id +
      (this.folder.parentFolderId ? '-' + this.folder.parentFolderId : '')
    );
  };

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  get areFiltersShown() {
    return this.folder.filters.length && !this.hideFilters;
  }

  onFilterChanged = (filter: Filter): void => {
    this.onFiltersUpdated.emit();
  };

  pinClicked = (): void => {
    this.onPinClicked.next();
  };

  clearFilters = (): void => {
    this.folder.filters.forEach((f) => f.clear());
    this.onFiltersUpdated.emit();
    const focusedElement = document?.activeElement as HTMLElement;
    focusedElement?.blur();
  };
}
