import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { ToastrService } from 'ngx-toastr';
import { Observable, Subscription } from 'rxjs';

import { DelayService, SessionStorageService, WindowRef } from 'shared-code-lib';
import { PageState } from '../../../../views/document-capture/capture-models';
import { PreviewPage } from '../document-preview.component';

declare const $: any;

@Component({
  selector: 'app-thumbnail',
  templateUrl: './thumbnail.component.html',
  styleUrls: ['./thumbnail.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class ThumbnailComponent implements OnInit, AfterViewInit, OnDestroy {
  @ViewChild('thumbnailWrapper', { static: true }) wrapperElementRef: ElementRef;
  @Input() page: PreviewPage;
  @Input() panelScrollbar: PerfectScrollbarDirective;
  @Input() onWidthResized: Observable<any>;
  @Output() getThumbnail = new EventEmitter<PreviewPage>();
  @Output() selectPage = new EventEmitter();

  isFaxMobile = false;

  PageState = PageState;

  private subscriptions = new Subscription();
  constructor(
    private translate: TranslateService,
    private toastr: ToastrService,
    private delay: DelayService,
    private windowRef: WindowRef,
    private sessionStorageService: SessionStorageService
  ) {}

  ngOnInit(): void {
    this.isFaxMobile = this.sessionStorageService.getIsInterFaxMobile();
    this.sessionStorageService.onIsInterFaxMobileChanged.subscribe((x) => (this.isFaxMobile = x));

    this.subscriptions.add(this.panelScrollbar.psScrollUp.subscribe(() => this.onThumbnailScrolled()));

    this.subscriptions.add(this.panelScrollbar.psScrollDown.subscribe(() => this.onThumbnailScrolled()));

    this.subscriptions.add(
      this.onWidthResized.subscribe(() => {
        this.onThumbnailScrolled();
        this.drawAnnotations();
        this.getThumbnail.emit(this.page);
      })
    );

    this.subscriptions.add(this.page.annotationsChanged.subscribe(() => this.drawAnnotations()));
  }

  ngAfterViewInit(): void {
    this.delay.execute(() => {
      if (this.isPageInViewPort(this.wrapperElementRef, false)) {
        this.getThumbnail.emit(this.page);
      }
    });
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  onThumbnailScrolled = (): void => {
    if (this.page.state !== PageState.Empty) return;

    if (!this.isPageInViewPort(this.wrapperElementRef, false)) return;

    this.getThumbnail.emit(this.page);
  };

  isPageInViewPort(element: ElementRef, fullyInView: boolean) {
    const pageTop = $(this.windowRef.nativeWindow).scrollTop();
    const pageBottom = pageTop + $(window).height();
    const elementTop = $(element.nativeElement).offset().top;
    const elementBottom = elementTop + $(element.nativeElement).height();

    if (fullyInView === true) {
      return pageTop < elementTop && pageBottom > elementBottom;
    } else {
      return elementTop <= pageBottom && elementBottom >= pageTop;
    }
  }

  select(): void {
    switch (this.page.state) {
      case PageState.Loading:
        this.toastr.warning(this.translate.instant('Common.PleaseWait'));
        break;
      case PageState.FailedLoad:
        return;
      default:
        this.selectPage.emit();
    }
  }

  drawAnnotations = (): void => {
    if (!this.wrapperElementRef || !this.page || !this.page.annotations) return;

    const canvas = this.wrapperElementRef.nativeElement.getElementsByTagName('canvas')[0];
    if (!canvas) return;
    const img = canvas.parentElement.getElementsByTagName('img')[0];
    if (!img) return;

    canvas.width = img.width;
    canvas.height = img.height;
    const ctx = canvas.getContext('2d');
    ctx.clearRect(0, 0, canvas.width, canvas.height);

    this.page.annotations
      .sort((a, b) => a.order - b.order)
      .forEach((annotation) => {
        annotation.draw(ctx, canvas.width, canvas.height, false);
      });
  };
}
