import {
  ChangeDetectorRef,
  Component,
  ElementRef,
  OnDestroy,
  OnInit,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { SortDescriptor } from '@progress/kendo-data-query';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { take, skip } from 'rxjs/operators';
import {
  CurrentSelectedDocumentService,
  DelayService,
  IColumn,
  ICombinedColumn,
  IDefaultColumn,
  InvokeAction,
  ItemTableDataSource,
  ItemsRequest,
  ItemsResult,
  MetaDataDefinition,
  Pagination,
  SortDirection,
  TableItem,
  UserPreferencesService,
} from 'shared-code-lib';
import { ListService } from '../../../../../../Bamba.Shared/src/fields/list/list.service';
import { AutoExecuteResult, ItemActionHandler, UpdateResults } from '../../blocks/core/itemActionHandler';
import { ActionService } from '../../blocks/services/action.service';
import { ApiService } from '../../blocks/services/api.service';
import { AutoExecuteActionsService } from '../../blocks/services/auto-execute-actions.service';
import { MyfilesService } from '../../blocks/services/myfiles.service';
import { SpinnerStateService } from '../../blocks/services/spinner-state.service';
import { UserStateService } from '../../blocks/services/user-state.service';
import { Preference } from '../document-capture/capture-models';
import { Filter, IFilterValue } from './../../blocks/models/filter';
import { FilterByInterfaxInboxPropertiesComponentLoadedService } from './../../blocks/services/FilterByInterfaxInboxPropertiesComponentLoaded.service';
import { FolderDetailsService } from './../../blocks/services/folder-details.service';
import { ActionTypes, Control, Folder, FolderDisplayMode, IAction, IFolder } from './../../blocks/services/models';
import { ProductItemRefreshService } from './../../blocks/services/product-item-refresh.service';
import { DocumentPreviewComponent, PreviewDocument } from './document-preview/document-preview.component';

@Component({
  selector: 'app-product-item',
  templateUrl: './product-item.component.html',
  styleUrls: ['./product-item.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class ProductItemComponent implements OnInit, OnDestroy {
  @ViewChild('spinnerState', { static: true }) spinnerWrapper: ElementRef;
  @ViewChild('documentPreview', { static: false }) documentPreview: DocumentPreviewComponent;

  constructor(
    private route: ActivatedRoute,
    private userState: UserStateService,
    private translate: TranslateService,
    private delay: DelayService,
    private api: ApiService,
    private apiService: ApiService,
    private toastr: ToastrService,
    private spinnerState: SpinnerStateService,
    private myfiles: MyfilesService,
    private elementRef: ElementRef,
    private cdr: ChangeDetectorRef,
    private userPreferencesService: UserPreferencesService,
    private currentSelectedDocumentService: CurrentSelectedDocumentService,
    private productItemRefreshService: ProductItemRefreshService,
    private actionService: ActionService,
    private autoExecuteActionsService: AutoExecuteActionsService,
    private folderDetailsService: FolderDetailsService,
    private filterByInterfaxInboxPropertiesComponentLoadedService: FilterByInterfaxInboxPropertiesComponentLoadedService,
    private listService: ListService
  ) {}

  formControls: Array<Control> = [];

  headerText = '';
  subscriptions = new Subscription();
  autoExecuteSubscriptions = new Subscription();

  datasource: ItemTableDataSource;
  columns: Array<ICombinedColumn>;
  tableItems: Array<TableItem>;
  selectedItems = new Array<TableItem>();
  itemActionHandler: ItemActionHandler;

  currentPage = 1;
  itemsPageCount = 0;
  itemsTotal = 0;
  pageSize = 5;

  loadingFailed: boolean;
  errors: Array<string> = [];

  sort: SortDescriptor;

  expandableDetail: boolean;
  loadingColumns = false;

  areFiltersPinned = true;
  firstLoad: boolean = true;

  _folder: IFolder;
  get folder() {
    return this._folder;
  }
  set folder(val) {
    this._folder = val;
    this.applyPinnedFilters();
  }

  showPreview: boolean;
  previewWidth: string;
  private readonly previewWidthPreferenceSuffix = 'preview-width';
  previewWidthPreferenceKey: string;
  keepSelectionOnLoad: boolean;
  supportActions: boolean = false;

  expandedItems: Array<{ id: string }> = [];

  dataLoaded: boolean;

  params: Params;

  private _previewDocument: PreviewDocument;
  get previewDocument() {
    return this._previewDocument;
  }
  set previewDocument(document: PreviewDocument) {
    this._previewDocument = document;
    this.currentSelectedDocumentService.setCurrentDocument(document);
  }

  canBrowserRenderDocument: boolean = false;
  get showDocumentPreview(): boolean {
    return this.canBrowserRenderDocument && this.showPreview;
  }

  ngOnInit() {
    this.itemActionHandler = this.actionService.createItemActionHandler();

    this.subscriptions.add(
      this.itemActionHandler.reload.subscribe((keepSelection: boolean) => {
        keepSelection ? this.refreshItems() : this.loadItems();
        this.loadAutoExecuteActions();
      })
    );

    this.subscriptions.add(
      this.itemActionHandler.update.subscribe((ur) => {
        if (ur.silent) {
          this.silentlyUpdateItems(ur);
        } else {
          this.refreshItems();
        }
      })
    );

    this.subscriptions.add(this.myfiles.filesUploaded.subscribe(this.loadItems));

    this.subscriptions.add(this.productItemRefreshService.onRefresh.subscribe(this.refreshItems));

    this.subscriptions.add(
      this.route.params.subscribe((params: Params) => {
        this.openFolder(params);
      })
    );

    this.subscriptions.add(
      this.itemActionHandler.onAutoExecuteResult.subscribe((result: AutoExecuteResult) => {
        this.autoExecuteSubscriptions.add(
          this.autoExecuteActionsService.setupAutoExecuteActions(result, this.folder, this.itemActionHandler)
        );
      })
    );

    this.handleInboundFolderFiltersLoading();

    this.setDocumentPreviewAvailability();
  }

  handleInboundFolderFiltersLoading = () => {
    if (
      [
        'InterFAX-Upland.Bamba.InterFAX.Inbox.Folders.Inbox',
        'InterFAX-Upland.Bamba.InterFAX.Inbox.Folders.Archived',
        'InterFAX-Upland.Bamba.InterFAX.Inbox.Folders.Trash',
      ].includes(this.route.snapshot.params.folder)
    ) {
      this.subscriptions.add(
        this.listService.overallLoadingChanged$.pipe(skip(1), take(2)).subscribe((loading) => {
          if (loading) {
            this.spinnerState.show(this.translate.instant('Common.LoadingData'), document.documentElement);
          } else {
            this.spinnerState.hide(this.translate.instant('Common.LoadingData'));
          }
        })
      );
    }
  };

  silentlyUpdateItems = (ur: UpdateResults) => {
    if (!ur.silent) return;

    const { tableItems, selectedItems } = this.autoExecuteActionsService.silentlyUpdateItems(
      ur,
      this.tableItems,
      this.selectedItems,
      this.columns,
      this.expandedItems
    );

    this.tableItems = tableItems;
    this.selectedItems = selectedItems;
  };

  onLoadPage = (pagination: Pagination): void => {
    this.currentPage = pagination.pageNumber;
    this.pageSize = pagination.pageSize;
    this.loadItems();
  };

  onPinClicked = (): void => {
    this.areFiltersPinned = !this.areFiltersPinned;

    if (!this.areFiltersPinned) {
      this.clearPinnedFilters();
    } else {
      this.updatePinnedFilters();
    }
  };

  onFiltersUpdated = (): void => {
    if (!this.dataLoaded) return;
    if (this.areFiltersPinned) this.updatePinnedFilters();

    this.currentPage = 1;
    this.refreshItems();
  };

  onSaveSelectedColumns = (columns: Array<IDefaultColumn>): void => {
    this.spinnerState.show(this.translate.instant('Common.LoadingData'), document.documentElement);

    this.userPreferencesService.setPreference('columns-' + this.getPreferencesKey(), columns);

    this.loadingColumns = true;

    this.delay.execute(() => {
      this.columns = this.getColumns();
      this.tableItems.forEach((ti) => ti.buildColumnValues(this.columns, this.translate));
      this.loadingColumns = false;
      this.spinnerState.hide();
    });
  };

  onSaveColumnWidths = (columns: Array<IDefaultColumn>): void => {
    this.userPreferencesService.setPreference('columns-' + this.getPreferencesKey(), columns);
  };

  onSort = (sd: SortDescriptor): void => {
    this.sort = sd;

    this.loadItems();

    this.userPreferencesService.setPreference('folder-sort-' + this.getPreferencesKey(), this.sort);
  };

  onPageSizeChanged = (pageSize: number): void => {
    this.userPreferencesService.setPreference('folder-pagesize-' + this.getPreferencesKey(), pageSize);
    this.keepSelectionOnLoad = true;
  };

  loadItems = (): void => {
    if (this.firstLoad && this.folder.filters.find((f) => f.id.endsWith('ByInterFAXInboxProperties'))) {
      this.filterByInterfaxInboxPropertiesComponentLoadedService.componentLoaded$.subscribe(() => {
        this.loadItemsEx();
        this.firstLoad = false;
      });
    } else {
      this.loadItemsEx();
    }
  };

  loadItemsEx() {
    const request = new ItemsRequest();
    const sortColumn = this.columns.find((c) => c.id == this.sort.field);
    const sortByField = sortColumn ? sortColumn.sortByFieldName : '';
    const sortByDirection = this.sort.dir == 'asc' ? SortDirection.Ascending : SortDirection.Descending;

    if (this.currentPage === 0) {
      this.currentPage = 1;
    }

    request.pageNumber = this.currentPage;
    request.pageSize = this.pageSize;
    request.sortBy = sortByField;
    request.sortByDirection = sortByDirection;
    request.filters = this.folder.filters.map((filter) => {
      const f = filter as Filter;
      return { id: f.id, value: f.value };
    });

    this.spinnerState.show(this.translate.instant('Common.LoadingData'), document.documentElement);
    this.datasource.loadItems(request, this.itemsLoad, this.itemsLoadFailed);
  }

  retainExpansionState = (items: TableItem[]): void => {
    if (!this.tableItems) return;

    for (const item of items) {
      for (const existing of this.tableItems) {
        if (item.data.id == existing.data.id) {
          item.expanded = existing.expanded;
          item.detailsTab = existing.detailsTab;
          item.selected = existing.selected;

          existing.expanded = false; // force ui to reload expansion part 1 #lame
        }
      }
    }

    this.cdr.detectChanges(); // force ui to reload expansion part 2 #lame
  };

  itemsLoad = (results: ItemsResult): void => {
    this.dataLoaded = true;
    this.spinnerState.hide(this.translate.instant('Common.LoadingData'));
    this.loadingFailed = !results.success;

    if (!results.success) {
      this.showPreview = false;
      this.errors = results.errors;
      return;
    }

    const items = results.items.map((i) => new TableItem(i, this.columns, this.translate));
    this.retainExpansionState(items);

    this.tableItems = items;
    if (this.keepSelectionOnLoad) {
      this.updateSelectedItemsAfterReload();
    } else {
      this.selectedItems = [];
      this.tableItems.forEach((c) => (c.selected = false));
      this.previewDocument = null;
    }

    this.keepSelectionOnLoad = false;

    this.itemsPageCount = results.items.length;
    this.itemsTotal = results.total;

    if (this.itemsTotal == 0) this.currentPage = 0;

    const gridContent = this.elementRef.nativeElement.getElementsByClassName('k-grid-content');
    if (gridContent && gridContent[0]) {
      gridContent[0].scrollTop = 0;
    }

    if (this.folder.showPreview && !this.showPreview) {
      this.openDocumentPreview();
    }

    for (const fd of results.folderData) {
      this.folderDetailsService.onFolderDetailsChanged(fd);
    }
  };

  updateSelectedItemsAfterReload() {
    this.selectedItems = this.tableItems.filter(
      (x) => this.selectedItems.findIndex((y) => y.data.id == x.data.id) != -1
    );

    if (this.selectedItems.length > 0) {
      this.previewDocument = new PreviewDocument(this.selectedItems[0], this.datasource, this.translate);
    } else {
      this.previewDocument = null;
    }
  }

  itemsLoadFailed = (): void => {
    this.spinnerState.hide(this.translate.instant('Common.LoadingData'));
    this.toastr.error(this.translate.instant('Common.LoadingDataError'));
  };

  ngOnDestroy() {
    this.subscriptions.unsubscribe();
    this.autoExecuteSubscriptions.unsubscribe();
  }

  onNewItemBar = (action: IAction) => {
    this.itemActionHandler.invoke(this.params.folder, action, [], this.getFilters());
  };

  onItemBar = (control: Control) => {
    this.itemActionHandler.invoke(this.params.folder, control.data, [], this.getFilters());
  };

  onInvokeAction = (ia: InvokeAction): void => {
    this.itemActionHandler.invoke(this.params.folder, ia.action, [ia.item], this.getFilters());
  };

  openDocumentPreview() {
    this.previewWidth = this.userPreferencesService.getPreference(this.previewWidthPreferenceKey);
    this.showPreview = true;
  }

  onWidthResize(p: Preference): void {
    this.documentPreview?.updatePreviewSize();
    this.userPreferencesService.setPreference(p.name, p.value);
  }

  onToolbarAction = (action: IAction) => {
    this.itemActionHandler.invoke(
      this.params.folder,
      action,
      this.selectedItems.map((ti) => ti.data),
      this.getFilters()
    );
  };

  getFilters = (): IFilterValue[] => {
    return this.folder.filters.map((filter) => {
      const f = filter as Filter;
      return { id: f.id, value: f.value };
    });
  };

  applyPinnedFilters = (): void => {
    if (!this.folder?.id) return;

    const pinnedFilters: IFilterValue[] = this.userPreferencesService.getPreference(
      this.getFolderPinUserPreferenceKey()
    );

    if (!pinnedFilters) {
      this.areFiltersPinned = true;
      return;
    } else if (pinnedFilters.length === 0) {
      this.areFiltersPinned = false;
      return;
    } else {
      this.areFiltersPinned = true;
    }

    this.folder.filters = this.folder.filters.map((f) => {
      const pinnedFilter = pinnedFilters.find((pf) => pf.id === f.id);
      if (pinnedFilter) f.value = pinnedFilter.value;

      return f;
    });
  };

  clearPinnedFilters = (): void => {
    this.userPreferencesService.setPreference(this.getFolderPinUserPreferenceKey(), []);
  };

  updatePinnedFilters = (): void => {
    const currentFilters: IFilterValue[] = this.folder.filters.map((f) => ({ id: f.id, value: f.value }));

    this.userPreferencesService.setPreference(this.getFolderPinUserPreferenceKey(), currentFilters);
  };

  private getFolderPinUserPreferenceKey = (): string => `folder-pinned-filter-${this.folder.id}`;

  onSelectionChanged = (): void => {
    this.selectedItems = this.tableItems.filter((ti) => ti.selected);

    if (this.selectedItems.length !== 1) {
      this.previewDocument = null;
      return;
    }

    if (this.previewDocument && this.previewDocument.item.data.id === this.selectedItems[0].data.id) return;

    const selectedItem = this.selectedItems[0];
    if (!selectedItem) return;

    this.previewDocument = selectedItem.data.documentExists
      ? new PreviewDocument(this.selectedItems[0], this.datasource, this.translate)
      : null;

    this.autoExecuteActionsService.setSelectedItem(selectedItem);
  };

  openFolder = (params: Params): void => {
    this.formControls = [];
    this.datasource = null;
    this.tableItems = null;
    this.currentPage = 1;
    this.itemsPageCount = this.itemsTotal = 0;
    this.headerText = '';
    this.expandableDetail = false;
    this.showPreview = false;
    this.previewDocument = null;
    this.params = params;
    this.selectedItems = [];

    if (params.folder.indexOf('@') != -1) {
      this.spinnerState.show(this.translate.instant('Common.LoadingData'), document.documentElement);
      this.apiService.getFolderById(params.folder, this.folderLoaded, this.folderLoadFailed);
    } else {
      this.folder = this.userState.findFolderById(params.folder);
      if (this.folder == null) return;
      this.reloadFolder();
    }
  };

  folderLoaded = (f: IFolder): void => {
    this.spinnerState.hide(this.translate.instant('Common.LoadingData'));
    this.folder = new Folder(f, this.userState.findProductById(f.product.id));
    this.reloadFolder();
  };

  folderLoadFailed = (e: any): void => {
    this.spinnerState.hide(this.translate.instant('Common.LoadingData'));
    console.log(`Failed to load folder ${e}`);
  };

  onLoadFolderSucceeded = (folder: IFolder): void => {
    this.folder = folder;
    this.onLoadFolder();
    this.spinnerState.hide(this.translate.instant('folder.loading-folder'));
  };

  onLoadFolderFailed = (): void => {
    this.toastr.error(this.translate.instant('Common.LoadingDataError'));
    this.spinnerState.hide(this.translate.instant('folder.loading-folder'));
  };

  onLoadFolder = (): void => {
    this.pageSize = this.userPreferencesService.getPreference('folder-pagesize-' + this.getPreferencesKey(), 25);

    this.expandableDetail = this.folder.supportsDetailsExpansion || this.folder.supportsVersions;
    this.supportActions = this.folder.supportActions;

    this.datasource = new ItemTableDataSource(this.folder.product.id, this.params.folder, this.api);
    this.columns = this.getColumns();

    this.setHeaderText();
    this.buildFormControls();
    this.loadAutoExecuteActions();

    const folderDefaultSort: SortDescriptor = {
      field: this.folder.defaultSortByColumn.columnId,
      dir: this.folder.defaultSortByColumn.direction == SortDirection.Ascending ? 'asc' : 'desc',
    };
    this.sort = this.userPreferencesService.getPreference('folder-sort-' + this.getPreferencesKey(), folderDefaultSort);

    this.previewWidthPreferenceKey = this.folder.id + this.previewWidthPreferenceSuffix;
    this.loadItems();
  };

  loadAutoExecuteActions = () => {
    let autoExecuteActions = this.folder.actions.filter((a) => (a.type & ActionTypes.AutoExecute) != 0);

    autoExecuteActions.forEach((a) => {
      this.itemActionHandler.invoke(this.folder.id, a, [], []);
    });
  };

  refreshItems = (): void => {
    this.keepSelectionOnLoad = true;
    this.loadItems();
  };

  getColumns(): Array<ICombinedColumn> {
    const columnPreferences: IDefaultColumn[] = this.userPreferencesService.getPreference(
      'columns-' + this.getPreferencesKey(),
      this.folder.defaultColumns
    );

    const folderColumns: IColumn[] = [];
    if (this.folder && this.folder.columns) {
      this.folder.columns.forEach((c) => folderColumns.push(c));
    }

    if (this.folder.includeMetaDataAsColumns) {
      this.userState.user.metaData.forEach((md) => folderColumns.push(MetaDataDefinition.asIColumn(md)));
    }

    const columns: Array<ICombinedColumn> = [];

    let preferredColumnsChanged = false;

    for (const pc of columnPreferences) {
      let correspondingColumn = folderColumns.find((fc) => pc?.id == fc?.id);

      if (!correspondingColumn) {
        correspondingColumn = folderColumns.find((fc) => pc?.id == fc?.headerText.replace(/ /g, '_').toUpperCase());

        if (correspondingColumn) {
          pc.id = correspondingColumn.id;
          preferredColumnsChanged = true;
        }

        if (!correspondingColumn) continue;
      }

      columns.push({ ...correspondingColumn, ...pc });
    }

    if (preferredColumnsChanged) {
      this.userPreferencesService.setPreference('columns-' + this.getPreferencesKey(), columnPreferences);
    }

    return columns;
  }

  buildFormControls = (): void => {
    this.formControls = [];
    for (const action of this.folder.actions) {
      if (action.type === ActionTypes.ItemBar) {
        const button = new Control(
          this.onItemBar,
          action.name,
          'btn btn-primary btn-minwidth',
          action.icon,
          '',
          'btn',
          false,
          null,
          action
        );

        this.formControls.push(button);
      }
    }

    const newItemBarActions = [];
    let menuTitle = '';

    for (const action of this.folder.actions) {
      if (action.type === ActionTypes.NewItemBar) {
        newItemBarActions.push(action);
      }

      if (action.type === ActionTypes.MenuTitle) {
        menuTitle = action.name;
      }
    }

    if (newItemBarActions.length) {
      const button = new Control(
        this.onNewItemBar,
        menuTitle,
        'btn btn-primary btn-minwidth',
        null,
        '',
        'menu',
        false,
        null,
        newItemBarActions
      );

      this.formControls.push(button);
    }
  };

  setHeaderText = (): void => {
    if (this.folder && this.folder.displayMode === FolderDisplayMode.TopLevel) {
      this.headerText = this.folder.name;
    } else {
      if (this.folder.headerText) {
        this.headerText = this.folder.headerText;
      } else {
        this.headerText = this.folder.product ? this.folder.product.name : '';
      }
    }
  };

  getPreferencesKey = (): string => {
    if (!this.folder) return '';

    let key = `${this.folder.product.id}-${this.folder.id}`;
    if (this.folder.parentFolderId) key += '-' + this.folder.parentFolderId;

    return key;
  };

  reloadFolder() {
    if (this.folder.forceReload) this.userState.addToForceReloadFoldersIds([this.params.folder]);

    if (this.userState.forceReloadFoldersIds.length) {
      this.reloadUserStateFolders();
      return;
    }

    this.onLoadFolder();
  }

  getFolder(folderId: string) {
    this.spinnerState.show(this.translate.instant('folder.loading-folder'), document.documentElement);
    this.userState.reloadFolder(this.params.folder, this.onLoadFolderSucceeded, this.onLoadFolderFailed);
  }

  reloadUserStateFolders() {
    const index: number = this.userState.forceReloadFoldersIds.findIndex((folderid) => folderid == this.params.folder);
    if (index == -1) {
      this.onLoadFolder();
      return;
    }

    this.getFolder(this.params.folder);
    this.userState.forceReloadFoldersIds.splice(index, 1);
  }

  setDocumentPreviewAvailability = () => {
    try {
      // Document preview doesn't work in browsers that don't support unicode property escapes /\p/.
      // https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/RegExp#browser_compatibility
      const r = new RegExp(/\p{Mn}/, 'u');
      this.canBrowserRenderDocument = true;
    } catch {
      this.canBrowserRenderDocument = false;
    }
  };

  getIFolderAsFolder = (): Folder => this.folder as Folder;
}
