import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class FolderDetailsService {
  folderDetailsChanged = new Subject<any>();

  constructor() {}

  onFolderDetailsChanged(folderDetails: any) {
    this.folderDetailsChanged.next(folderDetails);
  }
}
